import React, { Fragment } from 'react';
import { connect } from 'react-redux';
import { logout } from '../../../actions/auth';

import classes from './NavigationItems.module.css';
import NavigationItem from './NavigationItem/NavigationItem';

const navigationItems = ({ isAuthenticated, logout }) => {
    const authLinks = (
        <Fragment>
			<NavigationItem link="/admin">Account</NavigationItem>
			<NavigationItem link="/dashboard">Dashboard</NavigationItem>
			<li className='nav-item'>
                <a className='nav-link' onClick={logout} href=''>Logout</a>
            </li>
        </Fragment>
    );

    const guestLinks = (
        <Fragment>
			<NavigationItem link="/register">Register</NavigationItem>
			<NavigationItem link="/login">Login</NavigationItem>
        </Fragment>
    );

    return (
		<ul className={classes.NavigationItems}>
			<NavigationItem link="/" active>Home</NavigationItem>
			<NavigationItem link="/calculator">Calculator</NavigationItem>
			{ isAuthenticated ? authLinks : guestLinks }
		</ul>
    );
};

const mapStateToProps = state => ({
	isAuthenticated: state.auth.isAuthenticated
});


export default connect(mapStateToProps, { logout })(navigationItems);
